<template>
    <div class="app-container">
        <div class="public-account-management clearfix" v-loading="saveLoading">
            <!---->
            <div class="left">
                <div class="weixin-hd">
                    <div class="weixin-title">
                        {{menuName}}
                    </div>
                </div>
                <div class="weixin-menu menu_main clearfix">
                    <div class="menu_bottom" v-for="(item, i) of menu.button" :key="i" >
                        <!--  -->
                        <div @click="menuFun(i,item)" class="menu_item el-icon-s-fold" :class="{'active': isActive == i}">{{item.name}}</div>
                        <!---->
                        <div class="submenu" v-if="isSubMenuFlag == i">
                            <div class="subtitle menu_bottom" v-if="item.sub_button" v-for="(subItem, k) in item.sub_button" :key="k">
                                <div class="menu_subItem" :class="{'active': isSubMenuActive == i + '' +k}" @click="subMenuFun(subItem, i, k)">{{subItem.name}}</div>
                            </div>
                            <!--      5   -->
                            <div class="menu_bottom menu_addicon" v-if="!item.sub_button || item.sub_button.length < 5" @click="addSubMenu(i,item)"><i class="el-icon-plus"></i></div>
                        </div>
                    </div>
                    <!--  -->
                    <div class="menu_bottom menu_addicon" v-if="menuKeyLength < 3" @click="addMenu"><i class="el-icon-plus"></i></div>
                </div>
                <div class="save_div">
                    <!--<el-button class="save_btn" type="warning" size="small" @click="saveFun"></el-button>-->
                    <el-button class="save_btn" type="success" size="small" @click="saveAndReleaseFun"></el-button>
                </div>
            </div>
            <!---->
            <div v-if="showRightFlag" class="right">
                <div class="configure_page">
                    <div class="delete_btn">
                        <el-button size="mini"  type="danger" icon="el-icon-delete" @click="deleteMenu(tempObj)"></el-button>
                    </div>
                    <div>
                        <span></span>
                        <el-input class="input_width" v-model="tempObj.name" placeholder="" :maxlength=nameMaxlength clearable></el-input>
                    </div>
                    <div v-if="showConfigurContent">
                        <div class="menu_content">
                            <span></span>
                            <el-select v-model="tempObj.type" clearable placeholder="" class="menu_option">
                                <el-option v-for="item in menuOptions" :label="item.label" :value="item.value" :key="item.value"></el-option>
                            </el-select>
                        </div>
                        <div class="configur_content" v-if="tempObj.type == 'view'">
                            <span></span>
                            <el-input class="input_width"  v-model="tempObj.url" placeholder="" clearable></el-input>
                        </div>
                        <div class="configur_content" v-if="tempObj.type == 'miniprogram'">
                            <div class="applet">
                                <span>appid</span>
                                <el-input class="input_width" v-model="tempObj.appid" placeholder="appid" clearable></el-input>
                            </div>
                            <div class="applet">
                                <span></span>
                                <el-input class="input_width" v-model="tempObj.pagepath" placeholder="pages/index" clearable></el-input>
                            </div>
                            <div class="applet">
                                <span></span>
                                <el-input class="input_width" v-model="tempObj.url" placeholder="" clearable></el-input>
                            </div>
                            <p class="blue">tips:</p>
                        </div>
                        <div class="configur_content" v-if="tempObj.type == 'article_view_limited'">
                            <el-row>
                                <div class="select-item" v-if="tempObj && tempObj.content && tempObj.content.articles">
                                    <WxNews :objData="tempObj.content.articles"></WxNews>
                                    <el-row class="ope-row">
                                        <el-button type="danger" icon="el-icon-delete" circle @click="deleteTempObj"></el-button>
                                    </el-row>
                                </div>
                                <div v-if="!tempObj.content || !tempObj.content.articles">
                                    <el-row>
                                        <el-col :span="24" style="text-align: center">
                                            <el-button type="success" @click="openMaterial"><i class="el-icon-circle-check el-icon--right"></i></el-button>
                                        </el-col>
                                    </el-row>
                                </div>
                                <el-dialog title="" :visible.sync="dialogNewsVisible" width="90%">
                                    <WxMaterialSelect :objData="{repType:'news'}" @selectMaterial="selectMaterial"></WxMaterialSelect>
                                </el-dialog>
                            </el-row>
                        </div>
                        <div class="configur_content" v-if="tempObj.type == 'click' || tempObj.type == 'scancode_waitmsg'">
                            <!-- <WxReplySelect :objData="tempObj" v-if="hackResetWxReplySelect"></WxReplySelect> -->
                            <el-input label="" v-model="tempObj.key"></el-input>
                        </div>
                    </div>
                </div>
            </div>
            <!---->
            <div v-if="!showRightFlag" class="right">
                <p></p>
            </div>
        </div>
    </div>
</template>
  <script>
    import request from "@/utils/request"
    export default {
        name: 'wxmenu',
        components: {
            // WxReplySelect,
            // WxNews,
            // WxMaterialSelect
        },
        data(){
            return{
                showRightFlag:false,//
                menu:{
                    button:[
                    ]
                },//
                isActive: -1,// 
                isSubMenuActive: -1,// 
                isSubMenuFlag: -1,// 
                tempObj:{},//
                tempSelfObj:{
                    //tempObjmenu
                },
                visible2: false,//  ""
                tableData:[], //,
                menuName:'',
                showConfigurContent:true,
                nameMaxlength:0,//
                menuOptions: [{
                    value: 'view',
                    label: ''
                }, {
                    value: 'miniprogram',
                    label: ''
                }, {
                    value: 'click',
                    label: ''
                }, {
                    value: 'article_view_limited',
                    label: ''
                }, {
                    value: 'scancode_push',
                    label: ''
                }, {
                    value: 'scancode_waitmsg',
                    label: ''
                }, {
                    value: 'pic_sysphoto',
                    label: ''
                }, {
                    value: 'pic_photo_or_album',
                    label: ''
                }, {
                    value: 'pic_weixin',
                    label: ''
                }, {
                    value: 'location_select',
                    label: ''
                }],
                dialogNewsVisible:false,
                saveLoading:false,
                hackResetWxReplySelect:false
            }
        },
        created() {
            this.getMenuFun()
        },
        mounted() {

        },
        filters:{
        },
        computed: {
        },
        methods:{
            deleteTempObj(){
                this.$delete(this.tempObj,'repName')
                this.$delete(this.tempObj,'repUrl')
                this.$delete(this.tempObj,'content')
            },
            openMaterial(){
                this.dialogNewsVisible = true
            },
            selectMaterial(item){
                if(item.content.articles.length>1){
                    this.$alert('', '', {
                        confirmButtonText: ''
                    })
                }
                this.dialogNewsVisible = false
                this.tempObj.article_id = item.articleId
                this.tempObj.mediaName = item.name
                this.tempObj.url = item.url
                item.mediaType = this.tempObj.mediaType
                item.content.articles = item.content.articles.slice(0,1)
                this.tempObj.content = item.content
            },
            getMenuFun(){
                request({
                    url: "/mp/menu/list",
                    methods: "get"
                }).then((res)=>{
                    res.data.buttons.forEach(res => {
                        var obj = {}
                        
                        for (let [key, value] of Object.entries(res)) {
                            if (key !== "subButtons") {
                                obj[key] = value
                            }else {
                                obj["sub_button"] = value
                            }
                        }
                        this.menu.button.push(obj)
                    })
                    console.log(this.menu);
                });
            },
            handleClick(tab, event){
                this.tempObj.mediaType = tab.name
            },
            saveAndReleaseFun(){
                this.$confirm('?', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                }).then(() => {
                    this.saveLoading = true
                    request({
                        url: "/mp/menu/create",
                        method:"post",
                        data: this.menu
                    }).then(res => {
                        if (res.code === 200) {
                            this.$message.success(res.message)
                        }
                    })
                    this.saveLoading = false
                }).catch(() => {
                })
            },
            // 
            menuFun(i, item){
                this.hackResetWxReplySelect = false//
                this.$nextTick(() => {
                    this.hackResetWxReplySelect = true//
                })
                this.showRightFlag = true;//
                this.tempObj = item;//flag
                this.tempSelfObj.grand = "1";//
                this.tempSelfObj.index = i;//
                this.isActive = i; //
                this.isSubMenuFlag = i; //
                this.isSubMenuActive = -1; //
                this.nameMaxlength = 4
                if(item.sub_button&&item.sub_button.length>0){
                    this.showConfigurContent = false
                }else{
                    this.showConfigurContent = true
                }
            },
            // 
            subMenuFun(subItem, index, k){
                this.hackResetWxReplySelect = false//
                this.$nextTick(() => {
                    this.hackResetWxReplySelect = true//
                })
                this.showRightFlag = true;//
                this.tempObj = subItem;//
                this.tempSelfObj.grand = "2";//
                this.tempSelfObj.index = index;//
                this.tempSelfObj.secondIndex = k;//
                this.isSubMenuActive = index + "" + k; //
                this.isActive = -1;//
                this.showConfigurContent = true;
                this.nameMaxlength = 7
            },
            // 
            addMenu(){
                let menuKeyLength = this.menuKeyLength
                let addButton = {
                    name: "",
                    sub_button: []
                }
                this.$set(this.menu.button,menuKeyLength,addButton)
                this.menuFun(this.menuKeyLength-1, addButton)
            },
            // 
            addSubMenu(i,item){
                if(!item.sub_button||item.sub_button.length<=0){
                    this.$set( item, 'sub_button',[])
                    this.$delete( item, 'type')
                    this.$delete( item, 'pagepath')
                    this.$delete( item, 'url')
                    this.$delete( item, 'key')
                    this.$delete( item, 'article_id')
                    this.$delete( item, 'textContent')
                    this.showConfigurContent = false
                }
                let subMenuKeyLength = item.sub_button.length;//key
                let addButton = {
                    name: ""
                }
                this.$set(item.sub_button,subMenuKeyLength,addButton);
                this.subMenuFun(item.sub_button[subMenuKeyLength], i, subMenuKeyLength)
            },
            //
            deleteMenu(obj){
                let _this = this;
                this.$confirm('?', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                }).then(() => {
                    _this.deleteData();// 
                    _this.tempObj = {};
                    _this.showRightFlag = false;
                    this.isActive = -1;
                    this.isSubMenuActive = -1;
                }).catch(() => {
                });
            },
            // 
            deleteData(){
                // 
                if(this.tempSelfObj.grand == "1"){
                    this.menu.button.splice(this.tempSelfObj.index,1);
                }
                // 
                if(this.tempSelfObj.grand == "2"){
                    this.menu.button[this.tempSelfObj.index].sub_button.splice(this.tempSelfObj.secondIndex, 1);
                }
                this.$message({
                    type: 'success',
                    message: '!'
                });
            }
        },
        computed:{
            // menuObj   3 
            menuKeyLength:function(){
                return this.menu.button.length;
            }
        }
    }
  </script>
 <style lang="less" scoped="scoped">
 /*  */
 .clearfix{*zoom:1;}
 .clearfix::after{content: "";display: table; clear: both;}
 div{
     text-align: left;
 }
 .weixin-hd{
     color: #fff;
     text-align: center;
     position: relative;
     bottom: 426px;
     left:0px;
     width: 300px;
     height:64px;
     background: transparent url(@/assets/images/menu_head.png) no-repeat 0 0;
     background-position: 0 0;
     background-size: 100%
 }
 .weixin-title{
     color:#fff;
     font-size:14px;
     width:100%;
     text-align: center;
     position:absolute;
     top: 33px;
     left: 0px;
 }
 .weixin-menu{
     background: transparent url(@/assets/images/menu_foot.png) no-repeat 0 0;
     padding-left: 43px;
     font-size: 12px
 }
 .menu_option{
     width: 40%!important;
 }
 .public-account-management{
     min-width: 1200px;
     width: 1200px;
     margin: 0 auto;
     .left{
         float: left;
         display: inline-block;
         width: 350px;
         height: 715px;
         background: url("@/assets/images/iphone_backImg.png") no-repeat;
         background-size: 100% auto;
         padding: 518px 25px 88px;
         position: relative;
         box-sizing: border-box;
         /**/
         .menu_main{
             .menu_bottom{
                 position: relative;
                 float: left;
                 display: inline-block;
                 box-sizing: border-box;
                 width: 85.5px;
                 text-align: center;
                 border: 1px solid #ebedee;
                 background-color: #fff;
                 cursor: pointer;
                 &.menu_addicon{
                     height: 46px;
                     line-height: 46px;
                 }
                 .menu_item{
                     height: 44px;
                     line-height: 44px;
                     text-align: center;
                     box-sizing: border-box;
                     width: 100%;
                     &.active{
                         border: 1px solid #2bb673;
                     }
                 }
                 .menu_subItem{
                     height: 44px;
                     line-height: 44px;
                     text-align: center;
                     box-sizing: border-box;
                     &.active{
                         border: 1px solid #2bb673;
                     }
                 }
             }
             i{
                 color:#2bb673;
             }
             /**/
             .submenu{
                 position: absolute;
                 width: 85.5px;
                 bottom: 45px;
                 .subtitle{
                     background-color: #fff;
                     box-sizing: border-box;
                 }
             }
         }
         .save_div{
             margin-top: 15px;
             text-align: center;
             .save_btn{
                 bottom: 20px;
                 left: 100px;
             }
         }
     }
     /**/
     .right{
         float: left;
         width: 63%;
         background-color: #e8e7e7;
         padding: 20px;
         margin-left: 20px;
         -webkit-box-sizing: border-box;
         box-sizing: border-box;
         .configure_page{
             .delete_btn{
                 text-align: right;
                 margin-bottom: 15px;
             }
             .menu_content{
                 margin-top: 20px;
             }
             .configur_content{
                 margin-top: 20px;
                 background-color: #fff;
                 padding: 20px 10px;
                 border-radius: 5px
             }
             .blue{
                 color:#29b6f6;
                 margin-top: 10px;
             }
             .applet{
                 margin-bottom: 20px;
                 span{
                     width: 20%;
                 }
             }
             .material{
                 .input_width{
                     width: 30%;
                 }
                 .el-textarea{
                     width: 80%
                 }
             }
         }
     }
 }
</style>
<!--UI-->
<style lang="less" scoped>
 .public-account-management{
     .el-input{
         width: 70%;
         margin-right: 2%;
     }
 }
</style>
<!---->
<style lang="scss" scoped>
 .pagination{
     text-align: right;
     margin-right: 25px;
 }
 .select-item{
     width: 280px;
     padding: 10px;
     margin: 0 auto 10px auto;
     border: 1px solid #eaeaea;
 }
 .select-item2{
     padding: 10px;
     margin: 0 auto 10px auto;
     border: 1px solid #eaeaea;
 }
 .ope-row{
     padding-top: 10px;
     text-align: center;
 }
 .item-name{
     font-size: 12px;
     overflow: hidden;
     text-overflow:ellipsis;
     white-space: nowrap;
     text-align: center;
 }
</style>